import { omit } from 'lodash';
import { ADD_PERSON_TO_FAVORITE, REMOVE_PERSON_FROM_FAVORITE } from '@store/constants/actionTypes';
import {getLocalStorage} from '@utils/localStorage';


const initialState = getLocalStorage('store');//нчальное значние будут данные из локалСтораге

const favoriteReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD_PERSON_TO_FAVORITE:
            return {
                ...state,
                ...action.payload,
            };
        case REMOVE_PERSON_FROM_FAVORITE:
            return omit(state, [action.payload]);
            //в action.payload у меня айдишник того персонажа,который удаляю, лодашевская функция omit удаляет данные из объекта по ключу
        default:
            return state;
    }
};



export default favoriteReducer;
